import React, { useState, useEffect } from 'react';
import './statysticsPage.css';
import { InfoField } from './InfoField';
import { PomodoroPlug } from './PomodoroPlug/PomodoroPlug';
import { DayStatystics } from './DayStatystics/DayStatystics';
import { PomodoroSatystics } from './PomodoroSatystics/PomodoroSatystics';
import finishTaskStore from '../../store/finishTaskStore';
import { hoursCounterFn } from '../TimerPage/HoursCounter/HoursCounter';

// charJS plugin activation
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale, // y
  Tooltip,
  Legend
} from 'chart.js'
import { Bar } from 'react-chartjs-2'

ChartJS.register(
  BarElement,
  CategoryScale,
  LinearScale, // y
  Tooltip,
  Legend
)

export function StatysticsPage() {

  let finishTaskArray = finishTaskStore.finishTasksArray
  //    MOBX taskStore    

  let [isLoaded] = useState(false) // isLoaded     ""    ,  taskArray[index].text  
  loadingStatus(finishTaskArray) //  -

  //   
  function loadingStatus(array) { //  
    if (array.length === 0) {
      isLoaded = false
    } else if (array.length !== 0) {
      isLoaded = true
    }

    return isLoaded
  }


  const [isPauseMinutesStyleActive, setPauseMinutesStyleActive] = useState('')
  const [isStopCounterStyleActive, setStopCounterStyleActive] = useState('')
  const [isFocusStyleActive, setFocusStyleActive] = useState('')

  let nowDate = new Date() //   

  function dayName(incomeDate) { // -      ()
    let dayNumber = incomeDate.getDay()
    let result

    if (dayNumber === 0) {
      return result = ''
    } else if (dayNumber === 1) {
      return result = ''
    } else if (dayNumber === 2) {
      return result = ''
    } else if (dayNumber === 3) {
      return result = ''
    } else if (dayNumber === 4) {
      return result = ''
    } else if (dayNumber === 5) {
      return result = ''
    } else if (dayNumber === 6) {
      // eslint-disable-next-line
      return result = ''
    }

  }

  let weekName = dayName(nowDate);

  function todayFilter(item) { //     
    let incomDate = new Date(item.finishDate)
    return incomDate.toLocaleDateString() === nowDate.toLocaleDateString()
  }

  let filtredToDayTaskArray = finishTaskArray.filter(todayFilter)  //     

  let pomodoreCounter = filtredToDayTaskArray.reduce((summ, i) => summ = summ + i.pomodore, 0) //          PomodoroSatystics

  let workMinuteCounter = filtredToDayTaskArray.reduce((summ, i) => summ = summ + i.time, 0) //      
  let workMinute = hoursCounterFn(filtredToDayTaskArray) //       DayStatystics


  let pauseMinuteCounter = filtredToDayTaskArray.reduce((summ, i) => summ = summ + i.pauseTime, 0)

  let pauseMinute = new Date(pauseMinuteCounter).getMinutes()

  useEffect(() => {
    if (pauseMinuteCounter !== 0) { //     pauseMinute + 

      setPauseMinutesStyleActive('active')
    } else if (pauseMinuteCounter === 0) {

      setPauseMinutesStyleActive('')
    }
  }, [pauseMinuteCounter]);


  //   stopCounter

  let stopCounter = filtredToDayTaskArray.reduce((summ, i) => summ = summ + i.stopCounter, 0)

  useEffect(() => {
    if (stopCounter !== 0) { //     stopCounter+ 

      setStopCounterStyleActive('active')
    } else if (stopCounter === 0) {

      setStopCounterStyleActive('')
    }
  }, [stopCounter]);

  //focus

  let focusPercent
  
  if (workMinuteCounter === 0) {
    focusPercent = 0
  } else if (workMinuteCounter !== 0) {
    focusPercent = ((workMinuteCounter / (workMinuteCounter + pauseMinuteCounter)) * 100).toFixed(0)
  }
  

  useEffect(() => {
    if (workMinuteCounter !== 0) { //     focusPercent+ 
      setFocusStyleActive('active')
    } else if (workMinuteCounter === 0) {
      
      setFocusStyleActive('')
    }
  }, [workMinuteCounter])




  function weekNumberFn(incomDate) { //-   
    let currentDate = incomDate
    let startDate = new Date(currentDate.getFullYear(), 0, 1);
    let days = Math.floor((currentDate - startDate) /
      (24 * 60 * 60 * 1000));

    let weekNumber = Math.ceil(days / 7);

    return weekNumber
  }

  let currentWeekNumber = weekNumberFn(nowDate) //    

  let weekDaysArray = mainActionFn(currentWeekNumber, finishTaskArray) //          [0,5,0,8,0,0,10];



  function mainActionFn(currentWeekNumber, taskArray) { //          [0,5,0,8,0,0,10];
    function currentWeekFilter(item) { //        
      return item.weekNumber === currentWeekNumber
    }


    let filtredCurentWeekTaskArray = taskArray.filter(currentWeekFilter) //  taskArray  

    let result = filtredCurentWeekTaskArray.reduce((prev, item) => { //     
      if (item.finishDate in prev) {
        prev[item.finishDate] += item.time
      } else {
        prev[item.finishDate] = item.time;
      }
      return prev;
    }, {})

    let minutesDateArray = [] //       

    Object.keys(result).forEach(id => {
      let newObj = {}
      newObj.finishDate = id;
      newObj.time = result[id]
      minutesDateArray.push(newObj) //       
    })


    let weekDaysArray = [60000, 60000, 60000, 60000, 60000, 60000, 60000]; //       // 60000 (1 .)  

    for (let item of minutesDateArray) { //      
      let index = new Date(item.finishDate).getDay() //  -   

      weekDaysArray[index] = item.time
    }


    return weekDaysArray //         

  }

  const [chartData, setChartData] = useState(weekDaysArray);


  const selectOptions = [
    { value: 'currentWeek', text: ' ' },
    { value: 'weekAgo', text: ' ' },
    { value: 'twoWeekAgo', text: '2  ' },
  ];



  const [selectInputValue, setSelectAddInputValue] = useState(selectOptions[0].value);
  function onChangeHandler(event) {

    setSelectAddInputValue(event.target.value)



    let prewiousWeek = new Date();
    prewiousWeek.setDate(nowDate.getDate() - 7)

    let twoWeekAgo = new Date();
    twoWeekAgo.setDate(nowDate.getDate() - 14)





    if (event.target.value === 'currentWeek') {
      currentWeekNumber = weekNumberFn(nowDate)
    }

    if (event.target.value === 'weekAgo') {
      currentWeekNumber = weekNumberFn(prewiousWeek)
    }

    if (event.target.value === 'twoWeekAgo') {
      currentWeekNumber = weekNumberFn(twoWeekAgo)
    }



    weekDaysArray = mainActionFn(currentWeekNumber, finishTaskArray) // 


    setChartData(weekDaysArray)
  }




  // chartJS Settings
  //   
  let today = new Date()
  let todayIndex = today.getDay()
  let backgroundcolor = [];
  for (let i = 0; i < chartData.length; i++) {
    if (chartData[i] === 60000) { backgroundcolor.push('#C4C4C4') }
    if (chartData[i] > 60000) { backgroundcolor.push('#EA8A79') }
  }
  backgroundcolor[todayIndex] = '#DC3E22'


  const data = {
    labels: ['', '', '', '', '', '', ''],
    datasets: [{
      data: chartData,
      borderWidth: 1,
      backgroundColor: backgroundcolor, //   
      hoverBackgroundColor: '#DC3E22',
    }]
  };

  const options = {
    scales: {
      y: {
        beginAtZero: true,
        position: 'right',
        background: "#F4F4F4",
        ticks: {
          //     Y
          callback: function (value, index, values) {


            let date = new Date(value)

            let result = [date.getUTCHours() !== 0 ? (date.getUTCHours() + ' ') : '', date.getUTCMinutes() + ' '].map(function (x) {
              return x
            }).join(" ")

            return result

          }
        }
      },
      x: {
        backgroundColor: "#ECECEC",
        ticks: {
          //       X
          color: (context, index) => {
            let today = new Date()
            let todayIndex = today.getDay()
            const ticksColors = ['#999', '#999', '#999', '#999', '#999', '#999', '#999']
            ticksColors[todayIndex] = '#DC3E22'
            return ticksColors
          }
        },
        grid: {
          drawOnChartArea: false,
        }
      }
    },
    plugins: {
      tooltip: {
        enabled: false,
        },
      legend: {
        display: false,
      },
      customCanvasBackgroundColor: {
        color: 'lightGreen',
      }      
    }
  }


  return (
    <>

      <div className="row statistics__row statistics__row--top justify-content-between">

        <h2 className="statistics__title col-1"> </h2>
        <select className="statistics__select col-1" id="statistics-select" onChange={onChangeHandler} value={selectInputValue}>
          {selectOptions.map(option => (
            <option key={option.value} value={option.value}>
              {option.text}
            </option>
          ))}
        </select>

      </div>
      <div className="row statistics__row justify-content-between statistics__row--center">
        <div className="statistics__wrapper col-3">
          <DayStatystics todayName={weekName} workedTime={workMinute} />
          {!isLoaded && <PomodoroPlug />}
          {isLoaded && <PomodoroSatystics pomodoreMetric={pomodoreCounter} />}
        </div>
        <div className="statistics__chart col-9">
          <Bar id='myBar'
            style={
              {
                height: '100%',
                backgroundColor: '#F4F4F4',
              }
            }
            data={data}
            options={options}
          >
          </Bar>
        </div>
      </div>
      <div className="row statistics__row statistics__row--bottom justify-content-between">

        <InfoField fieldInfo={""} fieldMetric={focusPercent + ' %'} activeClass={isFocusStyleActive} categoryClass={'info-field--focus'} iconName={"focus"} />
        <InfoField fieldInfo={"  "} fieldMetric={pauseMinute + " "} activeClass={isPauseMinutesStyleActive} categoryClass={'info-field--paused'} iconName={"pause"} />
        <InfoField fieldInfo={""} fieldMetric={stopCounter} activeClass={isStopCounterStyleActive} categoryClass={'info-field--stops'} iconName={"stops"} />

      </div>
    </>

  );
}
